.commenstSec {
    width: 100%;
    margin: 0 auto;
    background-color: black;
}

.commentsSec__btn {
    width: 60%;
    color: #fff !important;
    text-decoration: none;
    background: #000000;
    padding: 10px;
    margin-bottom: 10px;
    margin-top: 10px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    transition: all 0.4s ease 0s;
    font-size: large;
    cursor: pointer;
}

.commentsSec__btn:hover {
    background: #ff0000;
    letter-spacing: 1px;
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
    box-shadow: 5px 40px -10px rgba(0, 0, 0, 0.57);
    transition: all 0.4s ease 0s;
}